.about-me {
  margin-top: 50px;
}

.about-me h2 {
  animation: coloring 6s 100 linear forwards;
}

@keyframes coloring {
  0% {
    color: white;
  }
  50% {
    color: rgb(0, 255, 128);
  }

  100% {
    color: #2caeba;
  }
}

.about-me h2 .smile-icon {
  font-size: 50px;
  color: yellow;
  margin-top: 10px;
}

.about-icon {
  position: absolute;
  font-size: 50px;
  right: 80px;
}

.about-first {
  border: 1px solid #2caeba;
  border-radius: 10px;
  padding: 20px;
}

.about-first p {
  color: #696969;
}

@media (max-width : 991px) {

  .about-icon {
    position: absolute;
    font-size: 50px;
    right: 15px;
  }

}
