.my-skills {
    text-align: center;
    margin-top: 50px;
}

.my-skills h2 {
    animation: coloring 6s 100 linear  forwards;
    
}

@keyframes coloring {
    0% {
        color: #0f0f0f;
    }
    

    100% {
        color: #2caeba;
    }
}





.skills-icons {
    justify-content: center;
    display: grid;
    grid-template-columns: 200px 200px 200px;
    margin-top: 20px;
    
}



.skills-icons .icons {
    font-size: 100px;
    color: #2caeba;
    animation: moving 6s 100 linear   forwards;
}

@keyframes moving {
    
    100% {
        transform:rotate(360deg);
    }
}

@media (max-width : 991px) {

    .skills-icons {
        justify-content: center;
        display: grid;
        grid-template-columns: 200px 200px;
        margin-top: 20px;
        
    }



}