.contact-me {
    margin-top: 50PX;
    
}

.contact-me h2 {
    animation: coloring 6s 100 linear  forwards;
    
}

@keyframes coloring {
    0% {
        color: #0f0f0f;
    }
    

    100% {
        color: #2caeba;
    }
}

.contact-area {
    display: flex;
    justify-content: space-around;
    
}

.contact-info {
    margin-top: 70px;
    color: black;
    height: 150px;
    width: auto;
    font-weight: bolder;
}

.contact-info p {
    margin-top: 5px;
}

.contact-info a {
    color: black;
    transition: 1s all linear;
}

.contact-info a:hover {
    color: aquamarine;
    font-size: 18px;
}

.contact-area form {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 50px;
    opacity: .8;
}

input  {
    width: 500px;
    text-align: center;
    border-radius: 8px;
    height: 50px;
    
}

input::placeholder {
    color: #2caeba;
}
textarea {
    width: 500px;
    text-align: center;
    
    height: 100px;
    border-radius: 8px;
}
textarea::placeholder {
    color: #2caeba;
}

.contact-area form button {
    background-color: #2caeba ;
    color: #fff;
    border-radius: 8px;
    width: 500px;
    height: 50px;
}


@media (max-width : 991px) {
    .contact-area {
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        
    }

    textarea {
        width: 100%;
    }

    .contact-area form button {

        width: 100%;
    }

    input  {
        width: 100%;
        
        
    }
}








