
header {
    
    width: 100%;
    z-index: 1000;
    display: flex;
    position: relative;
    align-items: center;
    overflow: hidden;
}

video {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    opacity: 1;
    z-index: -1;
}

.overlay
{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #03a9f4;
    mix-blend-mode: overlay;
    opacity: .5;
    z-index: -1;
}

/* header-top  */
.header-top {
    position: relative;
    top: 0;
    z-index: 10;
    display: flex;
    width: 100%;
    justify-content: space-between;
}

.header-top img {
    width: 150px;
    height: 150px;
}

.nav-icon {
    display: none;
}

.header-top .nav-main {
    z-index: 10;
    color: #fff;
    margin-top: 10px;

}

.header-top .nav-main ul {
    display: flex;
    list-style: none;
    padding-right: 20px;
}

.nav-main ul li {
    padding: 10px;
    border-right: 1px solid #2caeba;
}

.nav-main ul li a {
        
    color: #fff;
    font-weight: 400;
    

}


/* header-bottom  */
.header-bottom {
    text-align: center;
    color: #fff;
    padding-bottom: 100px;
}

.header-bottom img {
    width: 200x;
    height: 200px;
    border-radius: 50%;
    
}

.header-bottom .btn {
    background-color: #2caeba;
    border: 1px solid #fff;
    width: 100px;
    height: 50px;
    color: #fff;
    margin-top: 20px;
    z-index: 1000;
}

.btn a {
    color: #fff;
}

/* mobile-part */

@media (max-width : 991px) {

    .header-top .nav-main {
        position: relative;
        right: -80px;
        transition: all .5s linear;
    }
    .header-top .nav-main ul {
        display: flex;
        flex-direction: column;
        background-color: #2caeba;
        position: absolute;
        width: 150px;
        opacity: .8;
        border-radius: 8px;
    }
    .nav-icon {
        display: block;
        color: #2caeba;
        z-index: 1000;
        position: relative;
        top: 45px;
        right: 48px;
        font-size: 50px;
    }
    .active {
        position: relative;
        right: 20px;
        margin-top: 10px;
        transition: all .5s linear;
    }

    .active ul {
        display: flex;
        flex-direction: column;
        background-color: #2caeba;
        position: absolute;
        width: 150px;
        opacity: .8;
        list-style: none;
        padding-right: 20px;
        border-radius: 8px;

    }

    .active ul li {
        
        padding: 10px;
        font-weight: 400;
        border-right: 1px solid #2caeba;


    }
    .active ul li a {
        
        color: #fff;
        font-weight: 400;
        

    }
}


