@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700&display=swap);
* {
  margin: 0 ;
  padding: 0;
  font-family: 'Poppins', sans-serif;
  box-sizing: border-box;
  
}

body {
  background-color: #ececec;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.container {
  width: 85%;
  margin: auto;
}


a {
  text-decoration: none;
}




header {
    
    width: 100%;
    z-index: 1000;
    display: -webkit-flex;
    display: flex;
    position: relative;
    -webkit-align-items: center;
            align-items: center;
    overflow: hidden;
}

video {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    opacity: 1;
    z-index: -1;
}

.overlay
{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #03a9f4;
    mix-blend-mode: overlay;
    opacity: .5;
    z-index: -1;
}

/* header-top  */
.header-top {
    position: relative;
    top: 0;
    z-index: 10;
    display: -webkit-flex;
    display: flex;
    width: 100%;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

.header-top img {
    width: 150px;
    height: 150px;
}

.nav-icon {
    display: none;
}

.header-top .nav-main {
    z-index: 10;
    color: #fff;
    margin-top: 10px;

}

.header-top .nav-main ul {
    display: -webkit-flex;
    display: flex;
    list-style: none;
    padding-right: 20px;
}

.nav-main ul li {
    padding: 10px;
    border-right: 1px solid #2caeba;
}

.nav-main ul li a {
        
    color: #fff;
    font-weight: 400;
    

}


/* header-bottom  */
.header-bottom {
    text-align: center;
    color: #fff;
    padding-bottom: 100px;
}

.header-bottom img {
    width: 200x;
    height: 200px;
    border-radius: 50%;
    
}

.header-bottom .btn {
    background-color: #2caeba;
    border: 1px solid #fff;
    width: 100px;
    height: 50px;
    color: #fff;
    margin-top: 20px;
    z-index: 1000;
}

.btn a {
    color: #fff;
}

/* mobile-part */

@media (max-width : 991px) {

    .header-top .nav-main {
        position: relative;
        right: -80px;
        transition: all .5s linear;
    }
    .header-top .nav-main ul {
        display: -webkit-flex;
        display: flex;
        -webkit-flex-direction: column;
                flex-direction: column;
        background-color: #2caeba;
        position: absolute;
        width: 150px;
        opacity: .8;
        border-radius: 8px;
    }
    .nav-icon {
        display: block;
        color: #2caeba;
        z-index: 1000;
        position: relative;
        top: 45px;
        right: 48px;
        font-size: 50px;
    }
    .active {
        position: relative;
        right: 20px;
        margin-top: 10px;
        transition: all .5s linear;
    }

    .active ul {
        display: -webkit-flex;
        display: flex;
        -webkit-flex-direction: column;
                flex-direction: column;
        background-color: #2caeba;
        position: absolute;
        width: 150px;
        opacity: .8;
        list-style: none;
        padding-right: 20px;
        border-radius: 8px;

    }

    .active ul li {
        
        padding: 10px;
        font-weight: 400;
        border-right: 1px solid #2caeba;


    }
    .active ul li a {
        
        color: #fff;
        font-weight: 400;
        

    }
}



.about-me {
  margin-top: 50px;
}

.about-me h2 {
  -webkit-animation: coloring 6s 100 linear forwards;
          animation: coloring 6s 100 linear forwards;
}

@-webkit-keyframes coloring {
  0% {
    color: white;
  }
  50% {
    color: rgb(0, 255, 128);
  }

  100% {
    color: #2caeba;
  }
}

@keyframes coloring {
  0% {
    color: white;
  }
  50% {
    color: rgb(0, 255, 128);
  }

  100% {
    color: #2caeba;
  }
}

.about-me h2 .smile-icon {
  font-size: 50px;
  color: yellow;
  margin-top: 10px;
}

