.skills-icons {
    -webkit-justify-content: center;
            justify-content: center;
    display: grid;
    grid-template-columns: 200px 200px 200px;
    margin-top: 20px;
    
}



.skills-icons .icons {
    font-size: 100px;
    color: #2caeba;
    -webkit-animation: moving 6s 100 linear   forwards;
            animation: moving 6s 100 linear   forwards;
}

@-webkit-keyframes moving {
    
    100% {
        -webkit-transform:rotate(360deg);
                transform:rotate(360deg);
    }
}

@keyframes moving {
    
    100% {
        -webkit-transform:rotate(360deg);
                transform:rotate(360deg);
    }
}

