.my-work {
    margin-top: 50PX;
    
}



.my-work h2 {
    animation: coloring 6s 100 linear  forwards;
    
}

@keyframes coloring {
    0% {
        color: #0f0f0f;
    }
    

    100% {
        color: #2caeba;
    }
}

.work-area {
    display: flex;
    justify-content: space-between;
}

.work-box {
    width: 220px;
    height: 500px;
    margin-top: 10px;
    background-color: rgba(0, 0, 0, 0.5);
    text-align: center;
    border-radius: 8px;
    border: 1px  #2caeba solid;
    
}

.work-box h4 {
    color: #fff;

}


.work-box img {
    width: 200px;
    height: 200px;
    margin-top: 20px;
    border-radius: 20px;
    border: 10px solid black;

}

.work-area  .info {
    font-size: 16px;
    color: #2caeba;
}

.work-area  p {

    font-size: 50px;
    color: #2caeba;

}
.work-area  a {
    
    font-size: 50px;
    color: #fff;
    padding-right: 20px;

}


@media (max-width : 991px) {

    .work-area {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .work-box {
        width: 240px;
        height: auto;
        margin-top: 10px;
        background-color: rgba(0, 0, 0, 0.5);
        text-align: center;
        border-radius: 8px;
        border: 1px  #2caeba solid;
        
    }
    
    .work-box img {
        width: 160px;
        height: 160px;
        margin-top: 20px;
        border-radius: 20px;
        border: 10px solid black;
    
    }

    

    .work-area  .info {
        font-size: 14px;
        color: #2caeba;
    }
    
    .work-area  p {
    
        font-size: 40px;
        color: #2caeba;
    

    }

    .work-area  a {

        font-size: 30px;
        
    
    }
}

.rec.rec-arrow {
    background-color: #2caeba;
}
.rec-carousel-item:focus {
    color: #2caeba;
}